import './Home.css'
import {motion} from 'framer-motion';
import { useState,useEffect } from 'react';

function Home() {

    const [circle1Active,setCircle1Active] = useState(false);
    const [circle2Active,setCircle2Active] = useState(true);
    const [rotation,setRotation] = useState(false);

    const [circle1Size,setCircle1Size]=useState(100);
    const [circle2Size,setCircle2Size]=useState(300);

    const [transitionEnd,setTransitionEnd]=useState(false);

    const circle1 = {
        height:circle1Size,
        width:circle1Size,
        marginLeft:350,
        marginTop:70,
        backgroundColor:'gray',
        borderRadius:200,
        position:'absolute',
        cursor:circle2Active?'pointer':'default'
    }
    const circle2 = {
        height:circle2Size,
        width:circle2Size,
        marginLeft:50,
        marginTop:130,
        backgroundColor:'black',
        borderRadius:200,
        cursor:circle1Active?'pointer':'default'
    }
    const circleContainer = {
        height:500,
        width:500,
        marginRight:50,
        marginTop:140,
    }


    const setActive = (val) => {
        if (val==='circle1' && !circle1Active){
            setCircle2Active(false)
            setCircle1Active(true)
        }
        if (val==='circle2' && !circle2Active){
            setCircle2Active(true)
            setCircle1Active(false)
        }
    }

    useEffect(()=>{
        setTimeout(function() {
            setTransitionEnd(true)
             }, 2000);
           },
       [])

    return(
        <div className="homeContainer">
            <div className='homeTitle'>
                <motion.h1 initial={{opacity:0}} 
                            animate={{opacity:1}} 
                            transition={{duration:1}}>MAKING ENDS MEET</motion.h1>
                <motion.div initial={{y:200}} animate={{y:0}} transition={{duration:0.3,delay:2}} className='callToActionContainer'>
                    <a href='#' className='callToAction'>GET STARTED</a>
                </motion.div>
            </div>
            <div className='homeAnimationContainer'>
            {!transitionEnd && <div style={circleContainer}>
                <motion.div initial={{scale:0}} animate={{scale:1}} transition={{duration:1,delay:0.8}} style={circle1}></motion.div>
                <motion.div initial={{scale:0}} animate={{scale:1}} transition={{duration:0.6,delay:0.8}} style={circle2}></motion.div>
            </div>}
            {transitionEnd && 
                <div style={circleContainer}>
                    <motion.div style={circle1} animate={{scale:circle1Active?3:1}} onClick={()=>setActive('circle1')}></motion.div>
                    <motion.div style={circle2} animate={{scale:circle1Active?1/3:1}} onClick={()=>setActive('circle2')}></motion.div>
                </div>
            }
            </div>
            
        </div>
    )
}

export default Home;